"use client";

import { useState } from "react";
import Window from "./Window";
import NavBar from "./NavBar";
import DesktopIcon from "./desktopIcon";
import AboutMe from "./aboutMe";

interface WindowState {
  id: string;
  title: string;
  content: React.ReactNode;
  position: { x: number; y: number };
}

export default function HomeScreen() {
  const [windows, setWindows] = useState<WindowState[]>([]);
  
  const openWindow = (id: string, title: string, content: React.ReactNode) => {
    // Check if window already exists
    if (windows.find(w => w.id === id)) return;
    
    // Calculate position to stack windows
    const offset = windows.length * 30;
    setWindows([...windows, {
      id,
      title,
      content,
      position: { x: offset, y: offset }
    }]);
  };

  const closeWindow = (id: string) => {
    setWindows(windows.filter(w => w.id !== id));
  };

  return (
    <>
      <div className="relative min-h-screen w-full"
        style={{
          backgroundImage: `url(${'/assets/images/wallpaper.png'})`,
          backgroundSize: 'cover',
          backgroundPosition: 'center',
        }}
      >
        <DesktopIcon
        icon="/assets/icons/file_doc.png"
        label="CV-Resume"
        onDoubleClick={() => openWindow('cv', 'CV-Resume', <div>CV Content</div>)}
      />
      <DesktopIcon
        icon="/assets/icons/folder_programs.png"
        label="Projects"
        onDoubleClick={() => openWindow('projects', 'Projects', <div>Projects Content</div>)}
      />
      <DesktopIcon
        icon="/assets/icons/people.png"
        label="About Me"
        onDoubleClick={() => openWindow('about', 'About Me', <AboutMe />)}
      />

        {windows.map((window) => (
          <Window
            key={window.id}
            title={window.title}
            defaultPosition={window.position}
            onClose={() => closeWindow(window.id)}
            windowId={window.id}
          >
            {window.content}
          </Window>
        ))} 
      <NavBar />
      </div>
    </>
    )
} 