import Draggable from "react-draggable";
import { ReactNode } from "react";

interface WindowProps {
  title: string;
  children: ReactNode;
  onClose?: () => void;
  windowId: string;
  onMinimize?: () => void;
  onMaximize?: () => void;
  onHandle?: () => void;
  isActive?: boolean;
  defaultPosition?: { x: number; y: number };
}
const Window: React.FC<WindowProps> =({
  title,
  children,
  onClose,
  windowId,
  onMinimize,
  onMaximize,
  isActive,
  onHandle,
}) => (
  <div className="window-title-bar">
    <span className="window-title">{title}</span>
    <div className="window-controls">
      {onMinimize && (
        <button onClick={onMinimize}>
          <img src="/assets/icons/minimize.png" alt="Minimize" className="w-4 h-4" />
        </button>
      )}
      {onMaximize && (
        <button onClick={onMaximize}>
          <img src="/assets/icons/maximize.png" alt="Maximize" className="w-4 h-4" />
        </button>
      )}
      {onClose && (
        <button onClick={onClose}>
          <img src="/assets/icons/close.png" alt="Close" className="w-4 h-4" />
        </button>
      )}
      {(onHandle) && (
        <button onClick={onHandle}>
          <img src="/assets/icons/handle.png" alt="Handle" className="w-4 h-4" />
        </button>
      )}
      <div className="window-content">
        {children}
      </div>
    </div>
  </div>
)

export default Window;
